export default {
    name: "billslist",
    data(){
        return{
            bills:[],
            types:""
        }
    },
    mounted:function () {
        let token=JSON.parse(localStorage.getItem('user')).token;
        this.$axios.get('/user/invoices',
            {
                headers: {
                    'Authorization': 'Bearer ' + token,

                }
            }).then(res=>{
            this.bills=res.data.data;
            this.types=res.data.data.type;
            if(this.types=='1'){
                this.types="个人"
            }else if(this.types=='2'){
                this.types="公司"
            }

        })
    },

}



